@tailwind base;
@tailwind components;
@tailwind utilities;

.text--input {
  padding: 10px;
  font-family: "Karla", sans-serif;
  border-radius: 5px;
  border: 1px solid #d5d4d8;
  text-indent: 5px;
}

.remove--button {
  /* padding: 10px; */
  font-family: "Karla", sans-serif;
  border-radius: 5px;
  background: gray;
  color: white;
  border: none;
  cursor: pointer;
  flex: 20%;
}

.form--button {
  padding: 10px;
  font-family: "Karla", sans-serif;
  border-radius: 5px;
  background: gray;
  color: white;
  border: none;
  cursor: pointer;
}

.form--upload {
  margin: 0 0 10px 0;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.meme {
  height: 85vh;
}

.meme--text {
  position: absolute;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 0;
  padding: 0 3px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000,
    -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000,
    2px 2px 5px #000;
}

@media (max-width: 768px) {
  main {
    padding: 10px;
  }
  .header {
    padding: 10px;
  }

  .header--title {
    font-size: 1rem;
  }

  .header--project {
    font-size: 0.5rem;
  }

  main {
    padding: 10px;
  }

  .form {
    gap: 5px;
  }

  .form--button {
    padding: 10px;
    font-size: 0.75rem;
  }

  .text--input {
    padding: 5px;
    font-size: 0.75rem;
  }

  .remove--button {
    padding: 5px;
    font-size: 0.75rem;
  }

  .form--button {
    padding: 5px;
    font-size: 0.75rem;
  }

  .form--upload {
    padding: 2px;
  }

  .meme {
    position: relative;
  }

  .meme--text {
    font-size: 1.5em;
  }
}
